.logout{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: "Inter";
}
.logout_button{
    min-width: 300px;
    padding: 17px 30px;
    margin-top: 20px;
    background: blue;
    border: none;
    color: white;
    font-size: 20px;
    font-family: "Inter";
    border-radius: 3px;
}
.user_name{
    color: #8d0ada;
}