.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: "Inter";
}
.login_form{
    border: 2px solid black;
    padding: 20px;
    display: flex;
    flex-direction: column;
}
.login_form > h1{
    font-size: 35px;
    margin-bottom: 15px;
    padding-bottom: 10px;
    letter-spacing: -1px;
}
.login_form > input{
    min-width: 380px;
    padding: 20px 0;
    padding-left: 15px;
    margin-bottom: 10px;
    outline: none;
    border: 1px solid rgba(0,0,0,0.24);
    font-family: "Inter";
    border-radius: 5px;
    font-size: 20px;
}
.submit_btn{
    padding: 17px 30px;
    margin-top: 10px;
    background-color: green;
    border: none;
    color: white;
    font-size: 18px;
    font-family: "Inter";
    border-radius: 5px;
}



